.spark-checkbox-wrapper {
  display: -ms-flexbox;
  display: flex;
}
.spark-checkbox-wrapper .spark-checkbox {
  display: -ms-flexbox;
  display: flex;
  margin-top: 3px;
  width: 16px;
  height: 16px;
  margin-right: 10px;
  position: relative;
}
.spark-checkbox-wrapper .spark-checkbox .checkbox {
  width: 16px;
  height: 16px;
  margin: 0;
  z-index: 1;
  opacity: 0;
  cursor: pointer;
}
.spark-checkbox-wrapper .spark-checkbox .spark-checkbox-inner {
  width: 100%;
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: absolute;
  border: 1px solid rgba(177, 168, 168, 0.65);
  border-radius: 4px;
  text-align: center;
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
}
.spark-checkbox-wrapper.checked .spark-checkbox-inner {
  background: #1890ff;
  border-color: #1890ff;
  font-family: spark-icon;
  font-size: 4px;
}
.spark-checkbox-wrapper.checked .spark-checkbox-inner i {
  color: white;
}



